/** ANIMATIONS **/
.a-mi {
  transition: transform 1s ease-in-out;
  will-change: transform;
}
.a-mi--delayed {
  transition-delay: 1s;
}
.a-mi--left {
  transform: translateX(-200%);
}
.a-mi--left.is-loaded, .a-mi--left.is-active, .is-active .a-mi--left {
  transform: translateX(0);
}
.a-mi--right {
  transform: translateX(100%);
}
.a-mi--right.is-loaded {
  transform: translateX(0);
}
.a-mi--temporal--left {
  transform: translateX(-100%);
}
.a-mi--temporal--left.is-visible {
  transform: translateX(0);
}

.a-mi--zoom {
  transition: transform 1s linear;
  will-change: transform;
}
.a-mi--zoom--fast {
  transition-duration: 0.5s;
}
.a-mi--zoom--slow {
  transition-duration: 3s;
}
.a-mi--zoom--in {
  transform: scale(0.25);
}
.a-mi--zoom--out {
  transform: scale(2);
}
.a-mi--zoom.is-visible, .a-mi--zoom.is-loaded {
  transform: scale(1);
}

.a-mi--beat {
  transition: transform 20s linear;
  will-change: transform;
  transform: scale(1);
}
.a-mi--beat.is-visible {
  transform: scale(1.2);
}

.a-mi-bounce {
  transform: translateX(-200%);
}
.a-mi-bounce.is-loaded, .a-mi-bounce.is-active, .is-active .a-mi-bounce {
  animation: bounce-animation 1s ease-in-out forwards;
  will-change: transform;
}
.a-mi-bounce--delayed.is-loaded, .a-mi-bounce--delayed.is-active, .is-active .a-mi-bounce--delayed {
  animation-delay: 1s;
}

@keyframes bounce-animation {
  0% {
    transform: translateX(-2000px);
  }
  63% {
    transform: translateX(30px);
  }
  70%, 80%, 90%, 100% {
    transform: translateX(0px);
  }
  75% {
    transform: translateX(14px);
  }
  85% {
    transform: translateX(8px);
  }
  95% {
    transform: translateX(4px);
  }
}
.a-mi--color-grayscale {
  filter: grayscale(100%);
  transition: all 1.5s linear;
}
.a-mi--color-grayscale.is-all-visible {
  filter: grayscale(0%);
}

.a-mi-pulse {
  animation: pulse 1.25s infinite cubic-bezier(0.66, 0, 0, 1);
}
.a-mi-pulse--fast {
  animation: pulse 0.3s infinite cubic-bezier(0.62, 3.18, 0, -1.15);
}

.a-mi--float {
  animation: float 6s ease-in-out infinite;
}

@keyframes float {
  0% {
    transform: translatey(0px) scale(1);
  }
  50% {
    transform: translatey(-20px) scale(0.8);
  }
  100% {
    transform: translatey(0px) scale(1);
  }
}
